import meSelect from '@/components/me-select/me-select.vue'
export default {
	components: {
		meSelect
	},
	data() {
		return {
			 options: {
						flags: ['id'], //设置需要返回的参数这个参数必须在 dataList 中的item中存在（不配置默认全部返回）
						itemCanSelect: true //是否开启点击列表页选择配置
				},
				dataList: [{
								id: "1",
								select: false
						},
						{
								id: "2",
								select: false
						},
						{
								id: "3",
								select: false
						},
				]
		}
		},
		methods:{
			
			itemClick(e) {
					console.log('列表点击了：', e)
			},
			finish(e) {
					// console.log('所有的选择：', e)
					uni.showModal({
					    content: '删除此学生？',
							confirmColor:'#333333',
					    success:res=> {
					        if (res.confirm) {
					            // console.log('用户点击确定');
											uni.showToast({
												title:'删除成功'
											})
					        } else if (res.cancel) {
					            console.log('用户点击取消');
					        }
					    }
					});
			},
			change(e) {
					console.log('发生改变了：', e)
			}
		}
	
}
